import React from "react";

import trustpilot from "../../images/trustpilot.webp"
import google from "../../images/google.webp"
import bark from "../../images/bark.webp"
import clutch from "../../images/clutch.webp"

const Logoslider = () => {
  return (
    <section class="Logoslider" data-aos="fade-up" data-aos-duration="800">
    <div class="container">
      <ul class="row justify-content-around align-items-center header-icons  ">
        <li class="col-md-3"> <img src={trustpilot}/> </li>
        <li class="col-md-3"> <img src={google} /> </li>
        <li class="col-md-3"> <img src={bark}/> </li>
        <li class="col-md-3"> <img src={clutch}/> </li>
      </ul>
    </div>
  </section>
  );
};

export default Logoslider;
